import React, { useState, useRef } from "react"
import curriculumData from "../components/cs-curriculum.json"
import grades from "../components/grades.json"

import "bootstrap/dist/css/bootstrap.min.css"
import logo from "../images/AuWhite.png"
import Container from "react-bootstrap/Container"
import Navbar from "react-bootstrap/Navbar"
import "../pages/style.css"
import { Card } from "react-bootstrap"
import Form from "react-bootstrap/Form"
import Button from "react-bootstrap/Button"

function IndexPage() {
  const semesterRef = useRef("1")
  const yearRef = useRef("2020")
  const courses = useRef("Language Courses")
  const subject = useRef("BG 1001 English I")
  const gradeRef = useRef("A")

  const [selectedCourses,setSelectedCourses] = useState([])
  const [gpa, setGpa] = useState(0.0);

  const [groupName, setGroupName] = useState("")
  const [subjects, setSubjects] = useState([])
  const [code, setCode] = useState("")
  const [name, setName] = useState("")

  const handleSubjects = e => {
    const groupName = e.target.value
    setGroupName(groupName)
    console.log(groupName)
    const getSubjects = curriculumData.curriculum.subjects.find(
      group => group.groupName === groupName
    ).subjects
    setSubjects(getSubjects)
    // console.log(subjects)
  }

  function addInfo() {
    const info = {
      year: yearRef.current.value,
      semester: semesterRef.current.value,
      name: subject.current.value,
      grade: gradeRef.current.value,
    }
    info.gpa = matchGrade(info.grade)
    // console.log(subject.current.value)
    // console.log(courses.current)
    // console.log(info)
    selectedCourses.push(info);
    setSelectedCourses(selectedCourses);
    // console.log(selectedCourses)
    calculate()
    console.log(matchGrade(info.grade))
  }

  function calculate() {
    // e.preventDefault()
    console.log("THis is from calculate function")
    console.log(selectedCourses)
    let calGpa = 0.0
    for (var i = 0; i < selectedCourses.length; i++) {
      console.log(i)
      console.log(selectedCourses[i])
      
      switch (selectedCourses[i]) {
        case "A":
          calGpa += 4.0
          break

        case "A-":
          calGpa += 3.75
          break

        case "B+":
          calGpa += 3.25
          break

        case "B":
          calGpa += 3.0
          break

        case "B-":
          calGpa += 2.75
          break

        case "C+":
          calGpa += 2.25
          break

        case "C":
          calGpa += 2.0
          break

        case "C-":
          calGpa += 1.75
          break

        case "D":
          calGpa += 1.0
          break

        case "F":
          calGpa += 0.0
      }
    }

    calGpa /= selectedCourses.length
    setGpa(Math.round(calGpa*100) / 100)
  }

  const matchGrade = (gradeCode) => {
    return grades.grades.values.find(grade => grade.letter === gradeCode).digit
  }

  return (
    <>
      <Navbar className="color-nav" variant="dark">
        <Container>
          <Navbar.Brand href="#home">
            <img
              alt=""
              src={logo}
              width="80"
              height="70"
              className="d-inline-block"
            />{" "}
            <Navbar className="d-inline-block align-center fw-bold VMSGrade">
              {" "}
              VMS Grade Tracker
            </Navbar>
          </Navbar.Brand>
        </Container>
      </Navbar>
      <div className="mt-5">
        <h2 className="GPA-tittle">GPA Calculator</h2>
        <Button variant="warning CSButton fw-bold" size="lg">
          Computer Science
        </Button>
      </div>

      <div className="mt-5">
        <h2 className="GPA-tittle">Enter your course information</h2>
        <Card className="card-pos ">
          <Card.Body>
            <Form.Group className="mb-3 button-info" controlId="theYear">
              <Form.Label className="fw-bold">Year</Form.Label>
              <Form.Select ref={yearRef}>
                <option disabled selected style={{ color: 999 }}>
                  Select year
                </option>
                <option value="2020">2020</option>
                <option value="2021">2021</option>
                <option value="2022">2022</option>
                <option value="2023">2023</option>
              </Form.Select>
            </Form.Group>

            <Form.Group className="mb-3 button-info" controlId="theSemester">
              <Form.Label className="fw-bold">Semester</Form.Label>
              <Form.Select ref={semesterRef}>
                <option disabled selected style={{ color: 999 }}>
                  Select semester
                </option>
                <option value="1">1</option>
                <option value="2">2</option>
              </Form.Select>
            </Form.Group>

            <Form.Group className="mb-3 button-info" controlId="theCourses">
              <Form.Label className="fw-bold">Courses</Form.Label>
              <Form.Select onChange={e => handleSubjects(e)} ref={courses}>
                <option disabled selected style={{ color: 999 }}>
                  Select courses
                </option>
                {curriculumData.curriculum.subjects.map((getCourses, index) => (
                  <option value={getCourses.groupName} key={index}>
                    {getCourses.groupName}
                  </option>
                ))}
              </Form.Select>
            </Form.Group>

            <Form.Group className="mb-3 button-info" controlId="theSubjects">
              <Form.Label className="fw-bold">Subjects</Form.Label>
              <Form.Select ref={subject}>
                <option disabled selected style={{ color: 999 }}>
                  Select subject
                </option>
                {subjects.map((subjects, index) => (
                  <option
                    value={subjects.groupName}
                    key={index}
                  >{`${subjects.code} ${subjects.name}`}</option>
                ))}
              </Form.Select>
            </Form.Group>

            <Form.Group className="mb-3 button-info" controlId="theGrades">
              <Form.Label className="fw-bold">Grades</Form.Label>
              <Form.Select ref={gradeRef}>
                <option disabled selected style={{ color: 999 }}>
                  Select grade
                </option>
                <option value="A">A</option>
                <option value="A-">A-</option>
                <option value="B+">B+</option>
                <option value="B">B</option>
                <option value="B-">B-</option>
                <option value="C+">C+</option>
                <option value="C">C</option>
                <option value="C-">C-</option>
                <option value="D">D</option>
                <option value="F">F</option>
              </Form.Select>
            </Form.Group>

            <button type="button" class="btn btn-dark" onClick={addInfo}>
              + Add
            </button>

            <div class="mt-3 bdr">
        <table id="data-table" class="table table-bordered table-striped">
            <thead class="bg-gray text-left">
                <tr>
                    <th>Year</th>
                    <th>Semester</th>
                    <th>Name</th>
                    <th>Grade</th>
                    <th>GPA</th>
                  
                </tr>
            </thead>
            <tbody id="productBody" class="text-left">
               {selectedCourses.map((info, key) => {
                  <tr>
                    <td>info.year</td>
                    <td>info.semester</td>
                    <td>info.name</td>
                    <td>info.grade</td>
                    <td>info.gpa</td>
                  </tr>
               })}
            </tbody>

        </table>
    </div>



          </Card.Body>
        </Card>
      </div>
    </>
  )
}

export default IndexPage
